import React, { Component } from "react";
import { Link } from "react-router-dom";
import AddGame from "./addGame";
import Header from "./header";
import "./stylesheets/App.css";
import "./stylesheets/specifics.css";


const Game = props => (
  <Link to={"/editGame/" + props.game._id}>
    <div className="card">
      <div className="content">
        <span className="card-name">{props.game.name}</span>
        <span className="game-questions">15</span>
        <span className="game-time">10 min</span>
      </div>
    </div>
  </Link>
);

class EditTeam extends Component {
  constructor(props) {
    super(props);
    this.canceler = null;
    this.openModal.bind(this);
    this.fetchGames.bind(this);
    this.fetchClient.bind(this);

    this.state = {
      parentTeam: this.props.location.pathname.replace("/editTeam/games/", ""),
      parentTeamName: "",
      games: null,
      parentClientId: "",
      parentClientName: "",
      showModal: false,
      gameName: ""
    };
  }

  componentDidMount() {
    this.mounted = true;
    this.fetchGames();
    this.fetchClient();

    this.canceler = this.props.dbGame
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.fetchGames();
      });
    this.canceler = this.props.dbTeam
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.fetchClient();
      });
    this.canceler = this.props.dbClient
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.fetchClient();
      });
  }

  componentWillUnmount() {
    this.canceler.cancel();
    this.mounted = false;
  }

  fetchGames = () => {
    this.setState({
      games: null
    });
    this.props.dbGame
      .find({
        selector: {
          parentTeam: this.state.parentTeam
        }
      })
      .then(res => {
        const rows = res.docs;
        console.log(rows);
        this.setState({
          games: rows
        });
      })
      .catch(err => {
        console.log(err);
      });
  };

  fetchClient = () => {
    this.setState({
      parentClientId: ""
    });
    this.props.dbTeam
      .find({
        selector: {
          _id: this.state.parentTeam
        }
      })
      .then(res => {
        const rows = res.docs[0];
        this.setState({
          parentClientId: rows.parentClient,
          parentTeamName: rows.name
        });
        this.props.dbClient
          .find({
            selector: {
              _id: rows.parent
            }

          }).then(response => {
              const data = response.docs[0];
              this.setState({
                  parentClientName: data.name
              })
          }).catch((err) =>{
              console.log(err);
          });
      })
      .catch(err => {
        console.log(err);
      });
  };

  openModal(e) {
    e.preventDefault();
    this.setState({
      showModal: true
    });
  }

  showModal = e => {
    this.setState({
      showModal: !this.state.showModal
    });
  };

  onClose = e => {
    this.props.onClose && this.props.onClose(e);
  };

  onChangeGameName = e => {
    this.setState({
      gameName: e
    });
  };

  gameList() {
    return this.state.games.map((game, i) => <Game game={game} key={i} />);
  }

  render() {
    if (this.state.games === null) {
      return null;
    }

    return (
      <div>
        <Header />
        <Link className="info back" to={"/teams/" + this.state.parentClientId}>
          <h3>&lt;- Back to Teams</h3>
        </Link>
        <div className="info title">
          <h3>{this.state.parentClientName}</h3>
        </div>
        <div className="info add-container">
          <h3>{this.state.parentTeamName}</h3>
          <form>
            <input
              className="add-input"
              type="submit"
              value="Add Game +"
              onClick={this.openModal.bind(this)}
            />
          </form>
          <AddGame
            onClose={this.showModal}
            show={this.state.showModal}
            onChangeGameName={this.onChangeGameName}
            parentTeam={this.state.parentTeam}
            parentClient={this.state.parentClientId}
            db={this.props.dbGame}
          />
        </div>
        <ul className="nav">
          <h3 className="links">Games</h3>
          <Link
            className="links"
            to={"/editTeam/users/" + this.state.parentTeam}
          >
            Users
          </Link>
        </ul>

        <div className="storage-container">
          <div className="labels">
            <h5>{this.state.games.length} Active Games</h5>
            <h5 className="questions-label">Questions</h5>
            <h5 className="time-label">Completion Time</h5>
          </div>
          {this.gameList()}
        </div>
      </div>
    );
  }
}

export default EditTeam;
