import React, { Component } from "react";
import { useContext } from "react";
import { Link } from "react-router-dom";
import Header from "./header";
import UserContext from "./context/userContext";
import "./stylesheets/App.css";
import "./stylesheets/specifics.css";

const Game = props => (
  <Link to={"/editGame/" + props.game._id}>
    <div className="card">
      <div className="content">
        <span className="card-name">{props.game.name}</span>
        <span className="game-questions">15</span>
        <span className="game-time">10 min</span>
      </div>
    </div>
  </Link>
);

class GamesClass extends Component {
  constructor(props) {
    super(props);
    this.handleSubmit.bind(this);
    this.fetchGames.bind(this);
    this.fetchClient.bind(this);
    this.getUser = this.getUser.bind(this);

    this.state = {
      parentClient: this.props.location,
      userData: null,
      games: null,
      clientName: ""
    };
  }

  componentDidMount() {
    this.mounted = true;
    this.fetchGames();
    this.fetchClient();
    this.getUser();

    this.canceler = this.props.dbGame
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.fetchGames();
      });

    this.canceler = this.props.dbClient
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.fetchClient();
      });
    this.canceler = this.props.dbUser
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.getUser();
      });
  }

  componentWillUnmount() {
    this.canceler.cancel();
    this.mounted = false;
    this.setState({
      userData: null
    });
  }

  fetchClient = () => {
    this.setState({
      clientName: ""
    });
    this.props.dbClient
      .find({
        selector: {
          _id: this.state.parentClient
        }
      })
      .then(res => {
        const rows = res.docs[0].nameClient;
        this.setState({
          clientName: rows
        });
        console.log(this.state.clientName);
      })
      .catch(err => {
        console.log(err);
      });
  };

  fetchGames = () => {
    this.setState({
      games: null
    });
    this.props.dbGame
      .find({
        selector: {
          parentClient: this.state.parentClient
        }
      })
      .then(res => {
        const rows = res.docs;
        console.log(rows);
        this.setState({
          games: rows
        });
        console.log("Games " + this.state.games);
      })
      .catch(err => {
        console.log(err);
      });
  };

  getUser() {
    this.props.dbUser
      .find({
        selector: {
          email: this.props.userData
        }
      })
      .then(res => {
        const rows = res.docs[0];
        this.setState({
          userData: rows
        });
        console.log(this.state.userData);
      })
      .catch(err => {
        console.log(err);
      });
  }

  handleSubmit = e => {
    e.preventDefault();

    this.props.dbGame.put({
      _id: new Date().toJSON(),
      name: "untitled",
      parentClient: this.state.parentClient
    });
  };

  gameList() {
    return this.state.games.map((game, i) => <Game game={game} key={i} />);
  }

  render() {
    if (this.state.games === null) {
      return null;
    } else if (this.state.userData) {
      if (this.state.userData.admin || this.state.userData.gameEditor) {
        return (
          <div>
            <Header />
            {this.state.userData.admin ? (
              <div>
                <Link className="info back" to={"/clients"}>
                  <h3>&larr; Back to clients</h3>
                </Link>

                <div className="info title">
                  <h3>Clients</h3>
                </div>
              </div>
            ) : null}
            <div className="info add-container">
              <h3>{this.state.clientName}</h3>
              {this.state.userData.admin || this.state.userData.teamEditor ? (
                <form>
                  <input
                    className="add-input"
                    type="submit"
                    value="Add Game +"
                    onClick={this.handleSubmit.bind(this)}
                  />
                </form>
              ) : null}
            </div>
            <ul className="nav">
              {this.state.userData.admin ? (
                <Link
                  className="links"
                  to={"/contact/" + this.state.parentClient}
                >
                  Contact Information
                </Link>
              ) : null}

              {this.state.userData.admin || this.state.userData.teamEditor ? (
                <Link
                  className="links"
                  to={"/teams/" + this.state.parentClient}
                >
                  Teams
                </Link>
              ) : null}
              {this.state.userData.admin ? (
                <Link
                  className="links"
                  to={"/users/" + this.state.parentClient}
                >
                  Users
                </Link>
              ) : null}
              <h3 className="links">Games</h3>
              {this.state.userData.admin || this.state.userData.themeEditor ? (
                <Link
                  className="links"
                  to={"/themes/" + this.state.parentClient}
                >
                  Themes
                </Link>
              ) : null}
            </ul>
            <div className="storage-container">
              <div className="labels">
                <h5>{this.state.games.length} Active Games</h5>
                <h5 className="questions-label">Questions</h5>
                <h5 className="time-label">Completion Time</h5>
              </div>
              {this.gameList()}
            </div>
          </div>
        );
      } else {
        return null;
      }
    } else {
      return null;
    }
  }
}

export default function Games(props) {
  const { userData } = useContext(UserContext);
  if (!userData.user) {
    return null;
  }

  if (userData.user) {
    return (
      <GamesClass
        location={props.location.pathname.replace("/games/", "")}
        userData={userData.user.email}
        dbClient={props.dbClient}
        dbTeam={props.dbTeam}
        dbGame={props.dbGame}
        dbQuestion={props.dbQuestion}
        dbUser={props.dbUser}
        dbScore={props.dbScore}
        dbRating={props.dbRating}
      />
    );
  }
}
