import React, { Component } from "react";
import "./stylesheets/App.css";

class CreateUser extends Component {
  constructor(props) {
    super(props);
    this.fetchTeams = this.fetchTeams.bind(this);
    this.teamDropDown = this.teamDropDown.bind(this);
    this.state = {
      parentTeam: "",
      teams: null,
      firstname: "",
      lastname: "",
      email: "",
      admin: false,
      editor: false,
      analytics: false,
      game: false,
      team: false,
      theme: false,
      player: false
    };
  }

  componentDidMount() {
    this.mounted = true;
    this.fetchTeams();

    this.canceler = this.props.dbTeam
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.fetchTeams();
      });
  }

  componentWillUnmount() {
    this.canceler.cancel();
    this.mounted = false;
  }

  fetchTeams = () => {
    this.props.dbTeam
      .find({
        selector: {
          parentClient: this.state.parentClient
        }
      })
      .then(res => {
        const rows = res.docs;
        this.setState({
          teams: rows
        });
      })
      .catch(err => {
        console.log(err);
      });
  };

  onClose = e => {
    this.props.onClose &&
      this.props.onClose(e) &&
      this.props.onChangeFirstname(this.state.firstname) &&
      this.props.onChangeLastname(this.state.lastname) &&
      this.props.onChangeEmail(this.state.email) &&
      this.props.onChangeAdmin(this.state.admin) &&
      this.props.onChangeEditor(this.state.editor) &&
      this.props.onChangeAnalytics(this.state.analytics) &&
      this.props.onChangeGame(this.state.game) &&
      this.props.onChangeTeam(this.state.team) &&
      this.props.onChangeTheme(this.state.theme) &&
      this.props.onChangePlayer(this.state.player) &&
      this.props.onChangeParentTeam(this.state.parentTeam);
  };

  onChangeFirstname = e => {
    this.setState({
      firstname: e.target.value
    });
  };

  onChangeLastname = e => {
    this.setState({
      lastname: e.target.value
    });
  };

  onChangeEmail = e => {
    this.setState({
      email: e.target.value
    });
  };

  onChangeAdmin = e => {
    this.setState({
      admin: !this.state.admin,
      editor: false,
      analytics: false,
      game: false,
      team: false,
      theme: false,
      player: false
    });
  };

  onChangeEditor = e => {
    this.setState({
      admin: false,
      editor: !this.state.editor,
      analytics: true,
      game: true,
      team: true,
      theme: true,
      player: false
    });
  };

  onChangeAnalytics = e => {
    this.setState({
      analytics: !this.state.analytics,
      admin: false,
      player: false
    });
    if (
      !this.state.analytics &&
      this.state.game &&
      this.state.team &&
      this.state.theme
    ) {
      this.setState({
        editor: true
      });
    } else {
      this.setState({
        editor: false
      });
    }
  };

  onChangeGame = e => {
    this.setState({
      game: !this.state.game,
      admin: false,
      player: false
    });
    if (
      this.state.analytics &&
      !this.state.game &&
      this.state.team &&
      this.state.theme
    ) {
      this.setState({
        editor: true
      });
    } else {
      this.setState({
        editor: false
      });
    }
  };

  onChangeTeam = e => {
    this.setState({
      team: !this.state.team,
      admin: false,
      player: false
    });
    if (
      this.state.analytics &&
      this.state.game &&
      !this.state.team &&
      this.state.theme
    ) {
      this.setState({
        editor: true
      });
    } else {
      this.setState({
        editor: false
      });
    }
  };

  onChangeTheme = e => {
    this.setState({
      theme: !this.state.theme,
      admin: false,
      player: false
    });
    if (
      this.state.analytics &&
      this.state.game &&
      this.state.team &&
      !this.state.theme
    ) {
      this.setState({
        editor: true
      });
    } else {
      this.setState({
        editor: false
      });
    }
  };

  onChangePlayer = e => {
    this.setState({
      player: !this.state.player,
      admin: false,
      editor: false,
      analytics: false,
      game: false,
      team: false,
      theme: false
    });
  };

  onChangeParentTeam = e => {
    this.setState({
      parentTeam: e.target.value
    });
  };

  handleSubmit = e => {
    this.props.dbUser.put({
      _id: new Date().toJSON(),
      firstname: this.state.firstname,
      lastname: this.state.lastname,
      email: this.state.email,
      parentClient: this.props.parentClient,
      parentTeam: this.state.parentTeam,
      admin: this.state.admin,
      analyticsViewer: this.state.analytics,
      gameEditor: this.state.game,
      teamEditor: this.state.team,
      themeEditor: this.state.theme,
      playerOnly: this.state.player
    });
    this.onClose();
  };

  teamDropDown() {
    return this.state.teams.map((team, i) => {
      return (
        <option key={i} value={team._id}>
          {team.name}
        </option>
      );
    });
  }

  render() {
    if (!this.props.show) {
      return null;
    }
    return (
      <div className="add-client-modal-background">
        <div className="add-client-modal">
          <button onClick={this.onClose}>X</button>
          <h3>Create User</h3>
          <div className="form-container-client">
            <form>
              <label>FIRST NAME</label>
              <input
                type="text"
                placeholder="first"
                onChange={this.onChangeFirstname}
              />
            </form>
            <form>
              <label>LAST NAME</label>
              <input
                type="text"
                placeholder="last"
                onChange={this.onChangeLastname}
              />
            </form>
            <form>
              <label>EMAIL</label>
              <input
                type="text"
                placeholder="email"
                onChange={this.onChangeEmail}
              />
            </form>
          </div>
          <div className="form-container-client">
            <form>
              <label className="user-permissions">PERMISSIONS</label>
              <div className="user-radio-container">
                <div className="user-radio" onClick={this.onChangeAdmin}>
                  <div
                    className={
                      this.state.admin ? "default selected" : "default"
                    }
                  ></div>
                </div>
                <h4>Admin</h4>
              </div>
              <div className="user-radio-container">
                <div className="user-radio" onClick={this.onChangeEditor}>
                  <div
                    className={
                      this.state.editor ? "default selected" : "default"
                    }
                  ></div>
                </div>
                <h4>Editor</h4>
              </div>
              <div className="user-radio-container">
                <div className="user-radio" onClick={this.onChangeAnalytics}>
                  <div
                    className={
                      this.state.analytics ? "default selected" : "default"
                    }
                  ></div>
                </div>
                <h4>Analytics Viewer</h4>
              </div>
              <div className="user-radio-container">
                <div className="user-radio" onClick={this.onChangeGame}>
                  <div
                    className={this.state.game ? "default selected" : "default"}
                  ></div>
                </div>
                <h4>Game Editor</h4>
              </div>
              <div className="user-radio-container">
                <div className="user-radio" onClick={this.onChangeTeam}>
                  <div
                    className={this.state.team ? "default selected" : "default"}
                  ></div>
                </div>
                <h4>Team Editor</h4>
              </div>
              <div className="user-radio-container">
                <div className="user-radio" onClick={this.onChangeTheme}>
                  <div
                    className={
                      this.state.theme ? "default selected" : "default"
                    }
                  ></div>
                </div>
                <h4>Theme Editor</h4>
              </div>
              <div className="user-radio-container">
                <div className="user-radio" onClick={this.onChangePlayer}>
                  <div
                    className={
                      this.state.player ? "default selected" : "default"
                    }
                  ></div>
                </div>
                <h4>Player Only</h4>
              </div>
            </form>
            <form>
              <label>TEAM</label>
              <select
                value={this.state.parentTeam}
                onChange={this.onChangeParentTeam}
              >
                <option value="no team">No Team</option>
                {this.teamDropDown()}
              </select>
            </form>
          </div>
          <button onClick={this.handleSubmit}>Create User</button>
        </div>
      </div>
    );
  }
}

export default CreateUser;
